import React from "react";
import Message from "./Message";
import { User, users } from "../../../../data/users";
import { Box, Fab, Typography } from "@material-ui/core";
import { cn } from "@bem-react/classname";
import "./Message.css";

export interface ChatMessage {
  sender: User;
  text: string;
  time: string;
}

export const getRandomTime = (): string => {
  const hours = Math.floor(Math.random() * 12) + 1;
  const minutes = Math.floor(Math.random() * 60);
  const time = `${hours < 10 ? `0${hours}` : hours}:${
    minutes < 10 ? `0${minutes}` : minutes
  }`;
  return time;
};

interface MessageListProps {
  message: ChatMessage;
}

export const messages: ChatMessage[] = [
  {
    sender: users[0],
    text: "    ",
    time: getRandomTime(),
  },
  {
    sender: users[1],
    text: "",
    time: getRandomTime(),
  },
  {
    sender: users[2],
    text: "    ",
    time: getRandomTime(),
  },
  {
    sender: users[3],
    text: " !",
    time: getRandomTime(),
  },
  {
    sender: users[4],
    text: "",
    time: getRandomTime(),
  },
  {
    sender: users[5],
    text: " ,    ",
    time: getRandomTime(),
  },
  {
    sender: users[6],
    text: "  ",
    time: getRandomTime(),
  },
  {
    sender: users[7],
    text: " - ,   -.  ,   ,       .   -    -",
    time: getRandomTime(),
  },
  {
    sender: users[8],
    text: "",
    time: getRandomTime(),
  },
  {
    sender: users[9],
    text: ",  ?",
    time: getRandomTime(),
  },
  {
    sender: users[10],
    text: ",  ?",
    time: getRandomTime(),
  },
];

//class for block
const MsgList = cn("MessageList");

const MessageList = () => {
  return (
    <Box className={MsgList()}>
      <Box className={MsgList("InboxMsg")}>
        <Message message={" "} time={getRandomTime()} />
      </Box>
      <Box className={MsgList("OutgoingMsg")}>
        <Message message={""} time={getRandomTime()} />
      </Box>
      <Box className={MsgList("InboxMsg")}>
        <Message message={",  ?"} time={getRandomTime()} />
        <Message
          message={"    "}
          time={getRandomTime()}
        />
      </Box>
      <Box className={MsgList("OutgoingMsg")}>
        <Message message={""} time={getRandomTime()} />
      </Box>
      <Box className={MsgList("InboxMsg")}>
        <Message message={", "} time={getRandomTime()} />
      </Box>
      <Box className={MsgList("DateMsg")}>
        <Typography
          className={MsgList("DateChatItem")}
          variant="body2"
          align="center"
        >
          18 
        </Typography>
      </Box>
      <Box className={MsgList("InboxMsg")}>
        <Message message={" !"} time={getRandomTime()} />
      </Box>
      <Box className={MsgList("OutgoingMsg")}>
        <Message message={" !"} time={getRandomTime()} />
      </Box>
      <Box className={MsgList("InboxMsg")}>
        <Message
          message={
            " - ,   -. ,   ,       .   -    -"
          }
          time={getRandomTime()}
        />
      </Box>
    </Box>
  );
};

export default MessageList;
